<template>
  <div class="playPage" v-show="showPlayPage">
    <header>
      <i class="back fa fa-mail-reply" @click="$emit('togglePage')">
        <!-- 返回 -->
      </i>
      <div class="pageName">
        <h3 class="songName">{{ song?.name }}</h3>
        <div class="singerName" v-if="song?.song?.artists">
          <span v-for="name in song?.song?.artists" :key="name.id">
            {{ name.name }}
          </span>
        </div>
        <div class="singerName" v-else-if="song?.ar">
          <span v-for="name in song?.ar" :key="name.id">{{ name.name }}</span>
        </div>
      </div>
    </header>
    <section>
      <keep-alive>
        <PlayDisc
          v-if="showDisc"
          :song="song"
          :playing="playing"
          :lyric="lyric"
          :currentTime="currentTime"
          @click.native="showDisc = false"
        ></PlayDisc>
        <PlayLyric
          v-else
          :song="song"
          :playing="playing"
          :lyric="lyric"
          :currentTime="currentTime"
          @click.native="showDisc = true"
        ></PlayLyric>
      </keep-alive>
    </section>
    <footer>
      <div class="processtime">
        <span>{{ playtime(currentTime) }}</span>
        <label
          ><input
            type="range"
            name=""
            id=""
            min="0"
            :max="duration"
            :value="currentTime"
            @input="$emit('update-time', $event.target.value)"
        /></label>
        <span>{{ playtime(duration) }}</span>
      </div>
      <div class="pageControl">
        <i
          v-if="module == '顺序'"
          class="songControl iconfont icon-shunxubofang"
          @click="$emit('changeModule')"
        >
          <!-- {{ this.module }} -->
        </i>
        <i
          v-else-if="module == '单曲'"
          class="songControl iconfont icon-danquxunhuan"
          @click="$emit('changeModule')"
        >
          <!-- {{ this.module }} -->
        </i>
        <i
          v-else
          class="songControl iconfont icon-suijibofang"
          @click="$emit('changeModule')"
        >
          <!-- {{ this.module }} -->
        </i>
        <i
          class="songControl iconfont icon-shangyiqu"
          @click="$emit('change-song', 'prev')"
        >
          <!-- 上一 -->
        </i>
        <i
          class="songControl iconfont icon-zanting"
          v-if="!playing"
          @click="$emit('song-play')"
        >
          <!-- 播放 -->
        </i>
        <i
          class="songControl iconfont icon-bofangzhong"
          v-else
          @click="$emit('song-pause')"
        >
          <!-- 暂停 -->
        </i>
        <i
          class="songControl iconfont icon-xiayiqu"
          @click="$emit('change-song', 'next')"
        >
          <!-- 下一 -->
        </i>
        <i
          class="songControl iconfont icon-bofangliebiao"
          @click="$emit('showSongList')"
        >
          <!-- 列表 -->
        </i>
      </div>
    </footer>
  </div>
</template>

<script>
import PlayDisc from "@/components/PlayDisc.vue";
import PlayLyric from "@/components/PlayLyric.vue";
export default {
  components: {
    PlayDisc,
    PlayLyric,
  },
  props: {
    song: Object,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
    showPlayPage: Boolean,
    module: String,
    lyric: Array,
  },
  data: function () {
    return {
      showDisc: true,
      draging: false,
      tmpCurrentTime: this.currentTime,
    };
  },
  computed: {},
  methods: {
    // 分秒转换
    playtime(time) {
      let m = Math.floor(time / 60);
      let s = Math.floor(time % 60);
      if (m < 10) {
        m = "0" + m;
      }
      if (s < 10) {
        s = "0" + s;
      }
      return m + ":" + s;
    },
  },
  watch: {
    // currentTime: function (n) {
    //   if (!this.draging) {
    //     this.tmpCurrentTime = n;
    //   }
    // },
  },
};
</script>

<style lang="less" scoped>
.playPage {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: #f6f7fb;
  z-index: 999;
  top: 0;
  left: 0;
  header {
    display: flex;
    justify-content: center;
    position: relative;
    .back {
      position: absolute;
      top: 15rem;
      left: 15rem;
    }
    .pageName {
      padding-top: 26rem;
      text-align: center;
      width: 50%;

      .songName {
        font-size: 17rem;
        color: black;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      .singerName {
        padding: 5rem 0;
        font-size: 12.5rem;
        color: #818894;
        width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;

        text-align: center;

        span {
          &::after {
            content: "、";
          }
          &:last-child {
            &::after {
              content: "";
            }
          }
        }
      }
    }
  }
  section {
    width: 100%;
    height: 70%;
    // margin-top: 20rem;
  }
  footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    left: 0;
    display: flex;
    flex-direction: column;
    padding: 30rem 0;
    .processtime {
      width: 100%;
      display: flex;
      // justify-content: center;
      justify-content: space-around;
      padding: 0 15rem;
      align-items: center;
      span {
        width: 15%;
        text-align: center;
      }
      label {
        width: 70%;
        input {
          width: 100%;
        }
      }
    }
    .pageControl {
      display: flex;
      justify-content: space-around;
      margin-top: 10rem;
    }
  }
  i {
    // margin: 5rem;
    font-size: 30rem;
    color: #00baff;
  }
}
</style>