<template>
  <div class="search">
    <div class="search-top">
      <i class="search-icon"></i>
      <input
        type="text"
        class="search-input"
        v-model="value"
        placeholder="搜索歌曲、歌手、专辑"
        ref="input"
        @focus="searchDetails = null"
      />
      <i v-if="value" class="delete-icon" @click="deleteInfo"></i>
    </div>

    <div v-show="loading" class="loadingImg">
      <img
        src="data:image/gif;base64,"
        alt=""
      />
    </div>
    <!-- 热门搜索 -->
    <SearchHot
      v-show="!searchRes?.length"
      :hotTags="hotTags"
      @tagChangeValue="tagValue"
    ></SearchHot>
    <SearchHistory
      v-show="!searchRes?.length"
      :history="history"
      @deleteHistory="changeHistory"
      @searchHistory="searchHistory"
    ></SearchHistory>
    <!-- 搜索建议 -->
    <SearchInfo
      v-if="searchRes?.length && !searchDetails"
      :searchRes="searchRes"
      :value="value"
      @changeSuggest="changeSuggest"
    ></SearchInfo>
    <!-- 搜索详细 -->
    <SearchDetail
      v-if="searchDetails"
      :searchDetails="searchDetails"
      :searchBest="searchBest"
      :songId="songId"
      :playing="playing"
      @update-song="$emit('update-song', $event)"
      @update-song-list="$emit('update-song-list', $event)"
    ></SearchDetail>
  </div>
</template>

<script>
import SearchHot from "@/components/SearchHot.vue";
import SearchInfo from "@/components/SearchInfo.vue";
import SearchDetail from "@/components/SearchDetail.vue";
import SearchHistory from "@/components/SearchHistory.vue";
export default {
  components: {
    SearchHot,
    SearchInfo,
    SearchDetail,
    SearchHistory,
  },
  props: {
    playing: Boolean,
    songId: [String, Number],
  },
  data() {
    return {
      hotTags: [],
      value: "",
      searchRes: [],
      searchDetails: null,
      searchBest: {},
      loading: false,

      history: JSON.parse(window.localStorage.getItem("history")) || [],
    };
  },
  methods: {
    //输入框删除搜索信息
    deleteInfo: function () {
      this.value = "";
      this.searchDetails = null;
    },
    // 建议
    getSearchRes: function (v) {
      this.loading = true;
      this.axios
        .get("https://apis.netstart.cn/music/search/suggest", {
          params: {
            keywords: v,
            type: "mobile",
          },
        })
        .then((res) => {
          this.loading = false;
          this.value
            ? (this.searchRes = res?.data?.result?.allMatch)
            : (this.searchRes = []);
        });
    },
    // 详细歌曲
    getSearchDetails: function (v) {
      this.loading = true;
      this.axios
        .get("https://apis.netstart.cn/music/cloudsearch", {
          params: {
            keywords: v,
          },
        })
        .then((res) => {
          this.loading = false;
          this.searchDetails = res?.data?.result?.songs;
        });
      this.history = [...new Set([v, ...this.history])];
    },
    // 最佳匹配
    getSearchBest: function (v) {
      this.loading = true;
      this.axios
        .get("https://apis.netstart.cn/music/search/multimatch", {
          params: {
            keywords: v,
          },
        })
        .then((res) => {
          this.loading = false;
          this.searchBest = res?.data?.result;
        });
    },
    //点击建议搜索跳转
    changeSuggest: function (newValue) {
      this.value = newValue;
      this.getSearchDetails(newValue);
      this.getSearchBest(newValue);
    },
    //点击热门搜索跳转
    tagValue: function (newValue) {
      this.value = newValue;
      this.getSearchDetails(newValue);
      this.getSearchBest(newValue);
    },
    // 删除历史记录
    changeHistory: function (delhistory) {
      // console.log(delhistory);
      this.history = this.history.filter((o) => o !== delhistory);
    },
    //搜索历史记录
    searchHistory: function (newValue) {
      this.value = newValue;
      this.getSearchDetails(newValue);
      this.getSearchBest(newValue);
    },
  },
  //初始
  created: function () {
    this.loading = true;
    this.axios.get("https://apis.netstart.cn/music/search/hot").then((res) => {
      this.hotTags = res.data.result.hots;
      this.loading = false;
    });
    this.$nextTick(function () {
      this.$refs.input.focus();
    });
  },
  watch: {
    value: function (n) {
      if (n) {
        this.getSearchRes(n);
      } else {
        this.searchRes = [];
        this.searchDetails = null;
      }
    },
    history: function (newHistory) {
      localStorage.setItem("history", JSON.stringify(newHistory));
    },
  },
};
</script>

<style lang="less" scoped>
.search {
  .search-top {
    padding: 15rem 10rem;
    position: relative;
    .search-icon {
      background-image: url(data:image/svg+xml;base64,);
      width: 13rem;
      height: 13rem;
      position: absolute;
      left: 10rem;
      top: 50%;
      transform: translateY(-50%);
      margin: 0 8rem;
    }
    .search-input {
      width: 100%;
      height: 30rem;
      padding: 0 30rem;
      border-radius: 15rem;
      background: #ebecec;

      border: none;
      outline: none;
      line-height: 18rem;
      font-size: 14rem;
      &::placeholder {
        color: #c9c9c9;
      }
    }
    .delete-icon {
      background-image: url(data:image/svg+xml;base64,);
      width: 14rem;
      height: 14rem;
      position: absolute;
      right: 10rem;
      top: 50%;
      transform: translateY(-50%);
      margin: 0 8rem;
    }
    &::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 1rem;
      bottom: 0rem;
      left: 0;
      background-color: #ccc;
    }
  }
  .loadingImg {
    width: 100%;
    height: 50%;
    display: flex;
    justify-content: center;
    align-items: center;

    position: absolute;
    top: 30%;
    background-color: #fff;
    img {
    }
  }
}
</style>