<template>
  <div class="home">
    <div v-show="loading" class="loadingImg">
      <img
        src="data:image/gif;base64,"
        alt=""
      />
    </div>
    <div class="homeBanner">
      <img src="../assets/homeBanner.png" alt="" width="100%" />
    </div>

    <div class="songTitle">
      <h3>新歌首发</h3>
      <span>
        <router-link to="/hot">更多</router-link>
      </span>
    </div>
    <HomeSong
      :songId="songId"
      :playing="playing"
      :newsongs="newsongs"
      @update-song="$emit('update-song', $event)"
      @update-song-list="$emit('update-song-list', $event)"
    ></HomeSong>

    <div class="songTitle">
      <h3>热门歌单</h3>
      <span>
        <router-link to="/rank">更多</router-link>
      </span>
    </div>
    <ul class="card-list">
      <HomeCard
        v-for="card in personalizeds"
        :key="card.id"
        :card="card"
        @home-card-id="$emit('home-card-id-app', $event)"
      ></HomeCard>
    </ul>
  </div>
</template>

<script>
// @ is an alias to /src
import HomeCard from "@/components/HomeCard.vue";
import HomeSong from "@/components/HomeSong.vue";

export default {
  components: {
    HomeCard,
    HomeSong,
  },
  props: {
    songId: [String, Number],
    playing: Boolean,
  },
  data() {
    return {
      personalizeds: [],
      newsongs: [],
      loading: false,
    };
  },

  created: function () {
    this.loading = true;
    Promise.all([
      this.axios.get("https://apis.netstart.cn/music/personalized", {
        params: {
          limit: 6,
        },
      }),
      this.axios.get("https://apis.netstart.cn/music/personalized/newsong"),
    ]).then(([p, n]) => {
      // console.log(p,n);
      this.personalizeds = p.data.result;
      this.newsongs = n.data.result;
      this.loading = false;
    });
  },
};
</script>

