<template>
  <div class="playLyric" ref="playLyric">
    <ul ref="ul" v-if="lyric?.length">
      <li
        ref="li"
        v-for="(item, index) in lyric"
        :key="index"
        :style="`animation-duration:${nowTime}s;animation-play-state:${
          playing ? 'running' : 'paused'
        }`"
      >
        {{ item?.text }}
      </li>
    </ul>
    <p v-else>暂无歌词</p>
  </div>
</template>

<script>
export default {
  props: {
    song: Object,
    playing: Boolean,
    lyric: Array,
    currentTime: Number,
  },

  data() {
    return {
      nowTime: 0,
    };
  },
  computed: {
    // liHeight: function () {
    //   return [];
    // },
  },
  methods: {
    updateLyric: function () {
      if (this.currentTime !== 0 && this.lyric?.length !== 0) {
        var copy = [...this.lyric].reverse();
        var index =
          copy.findIndex((o) => o.time < this.currentTime) == -1
            ? copy.length - 1
            : copy.findIndex((o) => o.time < this.currentTime);
        var o = {
          index: copy.length - 1 - index,
          text: copy[index]?.text,
        };
        // console.log(index);
        // console.log(o);
        // console.log(copy);
        // 所有li的高度放入数组
        var liHeight = [];
        this.$refs.li.forEach((element, index) => {
          liHeight.push(element.offsetHeight);
          if (index === o.index) {
            // element.style.color = "#00baff";
            element.className = "load";
            // element.style.animationDuration =
            //   copy[index - 1].time - copy[index].time;
          } else {
            // element.style.color = "";
            element.className = "";
          }
        });
        this.nowTime = copy[index - 1].time - copy[index].time;
        // this.$refs.li.style.animationDuration =
        //   copy[index - 1].time - copy[index].time;
        // console.log(copy[index - 1].time - copy[index].time);

        if (
          // liHeight.slice(0, o.index)
          liHeight.slice(0, o.index).filter((o) => o != 0).length
        ) {
          // this.$refs.playLyric.scrollTop = liHeight
          //   .slice(0, o.index)
          //   .reduce((a, b) => {
          //     return a + b;
          //   });
          this.$nextTick(function () {
            this.$refs.playLyric.scrollTop = liHeight
              .slice(0, o.index)
              .reduce((a, b) => {
                return a + b;
              });
          });
        }
      } else {
        this.$refs.li.forEach((element, index) => {
          if (index === 0) {
            // element.style.color = "#00baff";
            element.className = "load";
            this.$refs.playLyric.scrollTop = 0;
          } else {
            // element.style.color = "";
            element.className = "";
          }
        });
      }
    },
  },
  watch: {
    currentTime: function () {
      if (this.lyric.length) {
        this.updateLyric();
      }
    },
  },
};
</script>

<style lang="less" scoped>
.playLyric {
  width: 100%;
  height: 100%;
  overflow: scroll;
  ul {
    // transform: translateY(190rem);
    margin-top: 190rem;
    padding-bottom: 235rem;

    li {
      width: 100%;
      text-align: center;
      color: black;
      font-size: 17rem;
      padding: 10rem 20rem;
      line-height: 25rem;
      // transition: all 0.2s;

      @keyframes scan {
        0% {
          background-size: 0 100%;
        }

        100% {
          background-size: 100% 100%;
        }
      }
      background: #7e7e7e -webkit-linear-gradient(left, #00baff, #00baff) no-repeat
        0 0;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      background-size: 0 100%;
      &.load {
        background-size: 100% 100%;
        animation: scan linear;
        &::before{
          content: "--";
        }
        &::after{
          content: "--";
        }
      }
    }
  }
  p {
    color: black;
    font-size: 30rem;
    text-align: center;
  }
}
</style>