<template>
  <div class="rank">
    <div v-show="loading" class="loadingImg">
      <img
        src="data:image/gif;base64,"
        alt=""
      />
    </div>
    <h3>推荐</h3>
    <ul class="recommendRankList">
      <li
        class="recommendRankItem"
        v-for="hotSonglist in hotSonglists.slice(0, 3)"
        :key="hotSonglist.id + 1"
      >
        <router-link :to="`/rankcardinfo?id=${hotSonglist?.id}`">
          <img
            :src="
              hotSonglist
                ? `${hotSonglist.coverImgUrl}?imageView=1&type=webp&thumbnail=108.8x0`
                : ''
            "
            alt=""
          />
          <div class="itemTracks">
            <div
              class="itemTrack"
              v-for="(track, index) in hotSonglist.tracks"
              :key="track.second + track.first"
            >
              {{ index + 1 }} {{ track.first }} -
              <span>{{ track.second }}</span>
            </div>
          </div>
        </router-link>
      </li>
    </ul>
    <h3>热门</h3>
    <ul class="hotRankList">
      <li
        class="hotRankItem"
        v-for="hotSonglist in hotSonglists"
        :key="hotSonglist.id"
      >
        <router-link :to="`/rankcardinfo?id=${hotSonglist?.id}`">
          <img
            :src="
              hotSonglist
                ? `${hotSonglist.coverImgUrl}?imageView=1&type=webp&thumbnail=108.8x0`
                : ''
            "
            alt=""
          />
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    // rankcard: Object,
  },
  data() {
    return {
      hotSonglists: [],
      loading: false,
    };
  },
  created: function () {
    this.loading = true;
    this.axios
      .get("https://apis.netstart.cn/music/toplist/detail")
      .then((res) => {
        this.hotSonglists = res.data.list;
        this.loading = false;
        // this.hotsongs = res.data.playlist.tracks.slice(0, 20);
      });
  },
};
</script>

<style lang="less" scoped>
.rank {
  margin: 0 15rem;
  .loadingImg {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    z-index: 99;
    left: 0;
    bottom: 0;
  }
  h3 {
    font-size: 17.68rem;
    color: #000;
    line-height: 50rem;
    font-weight: bold;
  }
  .recommendRankList {
    .recommendRankItem {
      display: flex;
      width: 100%;
      margin-top: 10rem;
      a {
        width: 100%;
        text-decoration: none;
        color: #000;
        display: flex;
      }
      &:first-child {
        margin-top: 0;
      }
      img {
        border-radius: 10rem;
        font-size: 0;
      }
      .itemTracks {
        width: 70%;
        display: flex;
        flex-direction: column;
        justify-content: center;

        // align-items: center;
        .itemTrack {
          width: 90%;
          margin-left: 12rem;
          margin-top: 10rem;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          span {
            color: #818894;
          }
        }
      }
    }
  }
  .hotRankList {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: space-between;
    .hotRankItem {
      //   margin: 0 auto;
      margin-bottom: 15rem;
      img {
        border-radius: 10rem;
        font-size: 0;
      }
    }
  }
}
</style>