<template>
  <!-- <div class="mask"> -->
  <div class="playList">
    <h3>当前播放歌单({{ playlist.length }})</h3>
    <div class="nolist" v-if="!playlist.length">
      <p class="nolist-title">空空如也...</p>
      <span>0(:3 )~ ('､3_ヽ)_</span>
    </div>
    <ul>
      <li
        v-for="item in playlist"
        :key="item.id"
        :style="{ color: songId == item.id ? '#00baff' : '' }"
        @click="$emit('chang-song', item)"
      >
        <p>{{ item.name }}</p>
        <!-- <i v-if="item.id !== songId"></i> -->
        <i
          v-if="item.id === songId"
          :class="{ playing: playing }"
          class="active"
        >
          <span></span>
          <span></span>
          <span></span>
          <span></span>
        </i>
      </li>
    </ul>
  </div>
  <!-- </div> -->
</template>

<script>
export default {
  props: {
    playlist: Array,
    playing: Boolean,
    songId: [String, Number],
  },
};
</script>

<style lang="less" scoped>
// .mask {
//   width: 100%;
//   height: 100vh;
//   background-color: rgba(0, 0, 0, 0.651);
//   position: fixed;
//   top: 0;
//   left: 0;
//   z-index: 99;
// }
.playList {
  width: calc(100% - 30rem);
  height: calc(100vh - 200rem);
  // height: 100vh;
  overflow: scroll;
  background-color: rgb(236, 236, 236);

  position: fixed;
  top: 100rem;
  bottom: 100rem;
  // bottom: 0;
  left: 0;
  padding: 20rem;
  margin: 0 15rem;
  z-index: 99999;

  // padding-bottom: 60rem;
  // margin-bottom: 60rem;
  border-radius: 30rem;
  h3 {
    font-size: 20rem;
  }
  .nolist {
    width: 100%;
    height: 30%;
    text-align: center;
    font-size: 30rem;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
  }
  ul {
    margin-top: 20rem;
    li {
      font-size: 17rem;
      width: 100%;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;

      display: flex;
      justify-content: space-between;
      align-items: center;
      // line-height: 1.5;
      height: 40rem;
      p {
        width: 80%;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      i {
        // font-size: 16.67rem;
        // width: 22rem;
        // height: 22rem;
        background-image: url(https://s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28…=);
        background-size: 166px 97px;
        background-position: -24px 0;
        width: 22rem;
        height: 22rem;
        // position: absolute;
        // right: 0rem;
        // top: 50%;
        // transform: translateY(-50%);
        &.active {
          background-image: none;
          display: flex;
          justify-content: space-around;
          width: 22rem;
          height: 22rem;
          span {
            @keyframes playingrunning {
              from {
                transform: rotateX(15deg);
              }
              to {
                transform: rotateX(60deg);
              }
            }
            width: 11%;
            height: 100%;
            background: #00baff;
            animation: playingrunning 0.8s infinite alternate;
            animation-play-state: paused;
            transform-origin: bottom;
            &:nth-child(1) {
              animation-delay: 0s;
            }
            &:nth-child(2) {
              animation-delay: -0.2s;
            }
            &:nth-child(3) {
              animation-delay: -0.4s;
            }
            &:nth-child(4) {
              animation-delay: -0.6s;
            }
          }
          &.playing {
            span {
              animation-play-state: running;
            }
          }
        }
      }
    }
  }
}
</style>