<template>
  <div class="rankCardView">
    <div v-show="loading" class="loadingImg">
      <img
        src="data:image/gif;base64,"
        alt=""
      />
    </div>
    <div class="top">
      <img :src="playlist?.playlist?.coverImgUrl" alt="" width="375.33" />
    </div>
    <div class="headtitle">共{{ playlist?.privileges?.length }}首</div>
    <ul>
      <li
        v-for="(playitem, index) in playitems"
        :key="playitem.id"
        @click="
          $emit('update-song', playitem);
          $emit('update-song-list', playitems);
        "
      >
        <div class="itemSort">{{ index + 1 }}</div>
        <div class="itmeInfo">
          <div
            class="name"
            :style="{ color: playitem.id === songId ? '#00BAFF' : '' }"
          >
            {{ playitem?.name }}
          </div>
          <div
            class="arname"
            :style="{ color: playitem.id === songId ? '#00BAFF' : '' }"
          >
            <span v-for="ar in playitem?.ar" :key="ar.id">{{ ar.name }}</span>
          </div>
        </div>
        <i v-if="playitem.id !== songId"></i>
        <i v-else :class="{ playing: playing }" class="active">
          <span></span>
          <span></span>
          <span></span>
          <span></span>
        </i>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    playing: Boolean,
    songId: [String, Number],
  },
  data() {
    return {
      loading: false,
      playlist: null,
      playitems: [],
      //   readyState: "",
    };
  },
  methods: {
    getPlayList: function (id) {
      this.loading = true;

      Promise.all([
        this.axios.get("https://apis.netstart.cn/music/playlist/detail", {
          params: {
            // limit: 6,
            id,
          },
        }),
        this.axios.get("https://apis.netstart.cn/music/playlist/track/all", {
          params: {
            // limit: 6,
            id,
          },
        }),
      ]).then(([d, t]) => {
        // console.log(p,n);
        this.playlist = d.data;
        this.playitems = t.data.songs;
        // this.newsongs = n.data.result;
        this.loading = false;
      });

      //   console.log(window.document.readyState);
    },
  },
  created: function () {
    this.getPlayList(this.$route.query.id);
    // console.log(this.$route.query.id);
  },
  //   beforeMount: function () {
  //     this.readyState = window.document.readyState;
  //   },
};
</script>

<style lang="less" scoped>
.rankCardView {
  margin-top: -124rem;
  .loadingImg {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    z-index: 99;
  }
  .top {
    font-size: 0;
    img {
      transform: translateY(-55rem);
    }
  }
  .headtitle {
    padding: 20rem;
    font-size: 17.68rem;
    color: #333;
    font-weight: bold;
    border-radius: 15rem 15rem 0 0;
    transform: translateY(-129rem);
    background-color: #fff;
  }
  ul {
    padding: 0 20rem;
    transform: translateY(-130rem);
    background-color: #fff;
    li {
      display: flex;
      width: 100%;
      justify-content: space-between;
      //   align-content: center;
      height: 73.28rem;
      align-items: center;
      .itemSort {
        font-size: 16.57rem;
        color: #818894;
        width: 2em;
        text-align: center;
        display: flex;
      }
      .itmeInfo {
        width: 80%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        .name {
          font-size: 16.57rem;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
        .arname {
          color: #818894;
          font-size: 13.26rem;
          line-height: 1.5;
          margin-top: 5rem;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          span {
            &::after {
              content: "、";
            }
            &:last-child {
              &::after {
                content: "";
              }
            }
          }
          //   display: flex;
        }
      }
      i {
        background-image: url(https://s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28…=);
        background-size: 166px 97px;
        background-position: -24px 0;
        width: 22rem;
        height: 22rem;
        &.active {
          background-image: none;
          display: flex;
          justify-content: space-around;
          width: 22rem;
          height: 22rem;
          span {
            @keyframes playingrunning {
              from {
                transform: rotateX(15deg);
              }
              to {
                transform: rotateX(60deg);
              }
            }
            width: 11%;
            height: 100%;
            background: #00baff;
            animation: playingrunning 0.8s infinite alternate;
            animation-play-state: paused;
            transform-origin: bottom;
            &:nth-child(1) {
              animation-delay: 0s;
            }
            &:nth-child(2) {
              animation-delay: -0.2s;
            }
            &:nth-child(3) {
              animation-delay: -0.4s;
            }
            &:nth-child(4) {
              animation-delay: -0.6s;
            }
          }
          &.playing {
            span {
              animation-play-state: running;
            }
          }
        }
      }
    }
  }
}
</style>