import Vue from 'vue';
import VueRouter from 'vue-router';
import HomeView from '../views/HomeView.vue';
import HotView from '../views/HotView.vue';
import SearchView from "../views/SearchView.vue";
import HomeCardView from "../views/HomeCardView.vue";
import RankView from "../views/RankView.vue";
import RankCardView from "../views/RankCardView.vue";

import ChineseSongView from "../views/ChineseSongView.vue";
import EuramericanSongView from "../views/EuramericanSongView.vue";
import KoreanSongView from "../views/KoreanSongView.vue";
import JapaneseSongView from "../views/JapaneseSongView.vue";


Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    redirect: '/home',
  },
  {
    path: '/home',
    // redirect: '/home',
    component: HomeView,
    meta: { showNav: true },
  },
  {
    path: '/hot',
    component: HotView,
    meta: { showNav: true },
    children: [
      {
        path: '',
        meta: { showNav: true },
        component: ChineseSongView
      },
      {
        path: 'en',
        meta: { showNav: true },
        component: EuramericanSongView
      },
      {
        path: 'k',
        meta: { showNav: true },
        component: KoreanSongView
      },
      {
        path: 'jp',
        meta: { showNav: true },
        component: JapaneseSongView
      },
    ]
  },
  {
    path: '/search',
    component: SearchView,
    meta: { showNav: true },
  },
  {
    path: '/cardInfo',
    component: HomeCardView,
  },
  {
    path: '/rank',
    meta: { showNav: true },
    component: RankView,
  },
  {
    path: '/rankcardinfo',
    // meta: { showNav: true },
    component: RankCardView,
  },
];

const router = new VueRouter({
  routes
});

export default router;
