<template>
  <div class="cardInfo">
    <div v-show="loading" class="loadingImg">
      <img
        src="data:image/gif;base64,"
        alt=""
      />
    </div>
    <div class="content" v-if="playlist">
      <div
        class="top"
        :style="playlist && `background-image: url(${playlist.coverImgUrl})`"
      >
        <div class="topInfo">
          <div class="noblur">
            <!-- <span class="listType">歌单</span>
            <span class="count">{{ count }}</span> -->
            <img class="listImg" :src="playlist.coverImgUrl" alt="" />
          </div>
          <div class="listRight">
            <div class="listTitle">{{ playlist.name }}</div>
            <div class="creator">
              <img
                :src="
                  playlist?.creator?.avatarUrl
                    ? `${playlist?.creator?.avatarUrl}?imageView=1&type=webp&thumbnail=25x0`
                    : ''
                "
                alt=""
                class="creatorImg"
              />
              <span class="creatorName">
                {{ playlist?.creator?.nickname }}
              </span>
            </div>
          </div>
        </div>
        <div class="topDescription">
          <span class="nomore" v-if="!takemore"
            >简介 : {{ playlist.description }}</span
          >
          <span v-else>简介 : {{ playlist.description }}</span>
          <img
            v-if="!takemore"
            src="../assets/downBtn.png"
            alt=""
            class="moreBtn"
            width="12rem"
            @click="toggleDescription"
          />
          <img
            v-else
            src="../assets/downBtn.png"
            alt=""
            class="moreBtn"
            width="12rem"
            style="transform: rotate(180deg)"
            @click="toggleDescription"
          />
        </div>
      </div>
      <h3 class="songListTag">共{{ playlist?.tracks.length }}首</h3>
      <ul class="songList">
        <li
          v-for="(songItem, index) in playlist?.tracks"
          :key="songItem.id"
          class="songItem"
          @click="
            $emit('update-song', songItem);
            $emit('update-song-list', playlist.tracks);
          "
        >
          <div class="itemSort">{{ index + 1 }}</div>
          <div class="itemInfo">
            <div
              class="itemName"
              :style="{ color: songItem.id === songId ? '#00BAFF' : '' }"
            >
              {{ songItem.name }}
            </div>
            <div
              class="itemDetail"
              :style="{ color: songItem.id === songId ? '#00BAFF' : '' }"
            >
              <span v-for="item in songItem.ar" :key="item.id">{{
                item.name
              }}</span>
              - {{ songItem.al.name }}
            </div>
          </div>
          <!-- <div class="itemPlay"></div> -->
          <div v-if="songItem.id !== songId" class="itemPlay"></div>
          <div v-else class="itemPlay active" :class="{ playing: playing }">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    // newsongs: Array,
    songId: [Number, String],
    playing: Boolean,
  },
  data() {
    return {
      playlist: null,
      loading: false,
      takemore: false,
    };
  },
  computed: {
    count: function () {
      if (this.playlist) {
        return this.playlist.playCount > 100000000
          ? `${(this.playlist.playCount / 100000000).toFixed(1)}亿`
          : this.playlist.playCount > 10000
          ? `${(this.playlist.playCount / 10000).toFixed(1)}万`
          : this.playlist.playCount;
      } else {
        return null;
      }
    },
  },
  methods: {
    getPlayList: function (id) {
      this.loading = true;
      this.axios
        .get("https://apis.netstart.cn/music/playlist/detail", {
          params: { id },
        })
        .then((res) => {
          // this.playlist = res.data.playlist;
          this.playlist = res.data.playlist;
          this.loading = false;
          // console.log(res);
        });
    },
    toggleDescription: function () {
      // console.log(1);
      this.takemore = !this.takemore;
    },
  },
  created: function () {
    this.getPlayList(this.$route.query.id);
    // console.log(this.$route.query.id);
  },
  // beforeRouteUpdate(to, from, next) {
  //   // 在当前路由改变，但是该组件被复用时调用
  //   // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
  //   // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
  //   // 可以访问组件实例 `this`
  //   // console.log(to.query.id);
  //   // this.getPlayList(to.query.id);
  //   console.log(to.query.id);
  //   next();
  // },
};
</script>

<style lang="less" scoped>
.cardInfo {
  margin-top: -124rem;
  .loadingImg {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    z-index: 99;
  }
  .top {
    padding: 30rem 10rem 30rem 15rem;
    display: flex;
    flex-direction: column;
    background-size: cover;
    background-position: center;
    position: relative;
    overflow: hidden;
    &::before {
      content: "";
      filter: blur(20rem);
      width: 100%;
      height: 100%;
      transform: scale(1.5);
      position: absolute;
      background: inherit;
      top: 0rem;
      left: 0rem;
      z-index: 0;
    }
    &::after {
      content: "";
      background-color: rgba(255, 255, 255, 0.86);
      width: 100%;
      height: 100%;
      transform: scale(1.5);
      position: absolute;

      top: 0rem;
      left: 0rem;
      z-index: 1;
    }
    .topInfo {
      display: flex;
      .noblur {
        position: relative;
        width: 126rem;
        height: 126rem;
        // flex-grow: 1;
        flex-shrink: 0;
        img.listImg {
          position: absolute;
          border-radius: 10rem;
          z-index: 9;
        }
      }
      .listRight {
        margin-left: 16rem;
        padding-top: 3rem;
        position: relative;
        z-index: 9;
        // flex-grow: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;
        .listTitle {
          padding-top: 1rem;
          font-size: 18.89rem;
          font-weight: bold;
          line-height: 1.3;
          color: #333333;
          // height: 44rem;
          display: -webkit-box; //限定超出两行省略
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          overflow: hidden;
        }
        .creator {
          margin-top: 15rem;
          display: flex;
          align-items: center;
          position: relative;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          .creatorImg {
            width: 25rem;
            height: 25rem;
            border-radius: 50%;
            margin-right: 10rem;
          }
          .creatorName {
            display: inline-block;
            color: #333333;
            font-size: 13.26rem;
          }
        }
      }
    }
    .topDescription {
      position: relative;
      z-index: 9;
      margin-top: 20rem;
      display: flex;
      flex-wrap: wrap;
      align-items: flex-start;
      span {
        font-size: 13.26rem;
        color: rgba(129, 136, 148, 1);
        line-height: 1.5;
        max-width: calc(100% - 15rem);
        &.nomore {
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
      }
      .moreBtn {
        margin-top: 2rem;
        font-size: 0;
      }
    }
  }
  .songListTag {
    font-size: 17.38rem;
    font-weight: bold;
    color: #333;
    background-color: #fff;
    padding: 20rem;
    border-radius: 15rem 15rem 0 0;
    transform: translateY(-10rem);
    // border-radius: 50%;
    // line-height: 23rem;
  }
  .songList {
    transform: translateY(-10rem);
    background-color: #fff;
    .songItem {
      display: flex;
      align-items: center;
      line-height: 73.28rem;
      .itemSort {
        font-size: 17rem;
        color: #999999;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 40rem;
      }
      .itemInfo {
        display: flex;
        flex-direction: column;
        padding: 6rem 0;
        width: 80%;
        .itemName {
          font-size: 16.57rem;
          line-height: 25.5rem;
          color: #000;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        .itemDetail {
          font-size: 13.26rem;
          line-height: 18rem;
          color: #818894;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          margin-top: 5rem;
          span {
            &::after {
              content: "、";
            }
            &:last-child {
              &::after {
                content: "";
              }
            }
          }
        }
      }
      .itemPlay {
        background-image: url(//s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28…=);
        background-repeat: no-repeat;
        background-size: 166px 97px;
        display: inline-block;
        width: 22rem;
        height: 22rem;
        background-position: -24px 0;
        &.active {
          background-image: none;
          display: flex;
          justify-content: space-around;
          width: 22rem;
          height: 22rem;
          span {
            @keyframes playingrunning {
              from {
                transform: rotateX(15deg);
              }
              to {
                transform: rotateX(60deg);
              }
            }
            width: 11%;
            height: 100%;
            background: #00baff;
            animation: playingrunning 0.8s infinite alternate;
            animation-play-state: paused;
            transform-origin: bottom;
            &:nth-child(1) {
              animation-delay: 0s;
            }
            &:nth-child(2) {
              animation-delay: -0.2s;
            }
            &:nth-child(3) {
              animation-delay: -0.4s;
            }
            &:nth-child(4) {
              animation-delay: -0.6s;
            }
          }
          &.playing {
            span {
              animation-play-state: running;
            }
          }
        }
      }
    }
  }
}
</style>