<template>
  <div id="app">
    <div class="topbox"></div>
    <nav v-if="$route.meta.showNav">
      <div class="navTop">
        <img
          src="./assets/homeLogo.png"
          alt=""
          width="94rem"
          height="25rem"
          class="homeLogo"
        />
        <div class="homeBtns">
          <span class="navBtn">下载酷狗</span>
          <span class="becomeVipBtn">
            <img
              src="./assets/homeVip.png"
              alt=""
              width="15rem"
              height="15rem"
              class="homeVip"
            />
            成为会员
          </span>
        </div>
      </div>
      <div class="homeTitle">
        <div class="titLeft">
          <div class="navTitle">
            <router-link to="/home" :class="{ active: active === '音乐' }"
              ><span @click="toggleNav">音乐</span></router-link
            >
          </div>
          <div class="navTitle">
            <router-link to="/hot" :class="{ active: active === '新歌' }"
              ><span @click="toggleNav">新歌</span></router-link
            >
          </div>
          <div class="navTitle">
            <router-link to="/rank" :class="{ active: active === '排行' }"
              ><span @click="toggleNav">排行</span></router-link
            >
          </div>
          <!-- <div class="navTitle">
            <router-link to="/">歌单</router-link>
          </div>
          <div class="navTitle">
            <router-link to="/">歌手</router-link>
          </div> -->
        </div>
        <div class="titRight">
          <router-link to="/search">
            <img
              src="./assets/homeSearch.png"
              alt=""
              width="17rem"
              height="17rem"
          /></router-link>
        </div>
      </div>
    </nav>
    <router-view
      @update-song="song = $event"
      @update-song-list="playlist = $event"
      @showSongList="showSongList = !showSongList"
      :songId="song?.id"
      :playing="playing"
    />
    <div class="zhanweibox"></div>
    <audio
      :src="songSrc"
      autoplay
      ref="audio"
      @play="playing = true"
      @pause="playing = false"
      @durationchange="duration = $event.target.duration"
      @timeupdate="currentTime = $event.target.currentTime"
    ></audio>
    <PlayBar
      :song="song"
      :playing="playing"
      :duration="duration"
      :currentTime="currentTime"
      @song-play="song && $refs.audio.play()"
      @song-pause="song && $refs.audio.pause()"
      @showSongList="showSongList = !showSongList"
      @click.native="
        song && !showSongList ? (showPlayPage = true) : (showPlayPage = false)
      "
    ></PlayBar>

    <PlayPage
      :song="song"
      :playing="playing"
      :duration="duration"
      :currentTime="currentTime"
      :module="module"
      :lyric="lyric"
      @song-play="song && $refs.audio.play()"
      @song-pause="song && $refs.audio.pause()"
      :showPlayPage="showPlayPage"
      @togglePage="showPlayPage = false"
      @showSongList="showSongList = !showSongList"
      @changeModule="changeModule"
      @change-song="changeSong"
      @update-time="$refs.audio.currentTime = $event"
    ></PlayPage>

    <PlayList
      v-show="showSongList"
      :playlist="playlist"
      :songId="song?.id"
      :playing="playing"
      @chang-song="song = $event"
    ></PlayList>
  </div>
</template>

<script>
import PlayBar from "@/components/PlayBar.vue";
import PlayPage from "@/components/PlayPage.vue";
import PlayList from "@/components/PlayList.vue";
export default {
  components: {
    PlayBar,
    PlayPage,
    PlayList,
  },
  data() {
    return {
      // cardId: null,
      song: null,
      duration: 0,
      currentTime: 0,
      playing: false,
      showPlayPage: false,
      showSongList: false,
      playlist: [],
      modules: ["顺序", "单曲", "随机"],
      module: "顺序",
      lyric: [],
      active: JSON.parse(window.localStorage.getItem("active")) || "音乐",
      // active: window.sessionStorage.getItem("active") || "音乐",
    };
  },
  methods: {
    changeModule: function () {
      var index = this.modules.findIndex((o) => o === this.module);
      // console.log(index);
      this.module =
        index === this.module.length
          ? this.modules[0]
          : this.modules[index + 1];
    },
    changeSong: function (direction) {
      // console.log(direction);
      if (this.module == "顺序" || this.module == "单曲") {
        var nowIndex = this.playlist.findIndex((o) => o.id === this.song.id);
        // console.log(nowIndex);
        if (direction === "next") {
          this.song =
            nowIndex === this.playlist.length - 1
              ? this.playlist[0]
              : this.playlist[nowIndex + 1];
          // console.log(this.song);
        } else {
          this.song =
            nowIndex === 0
              ? this.playlist[this.playlist.length - 1]
              : this.playlist[nowIndex - 1];
        }
      } else {
        var randomList = this.playlist.filter((o) => o.id !== this.song.id);
        this.song = randomList[Math.floor(Math.random() * randomList.length)];
      }
    },
    toggleNav: function (event) {
      // console.log(event.target);
      // console.log(event.target.textContent);
      this.active = event.target.textContent;
      localStorage.setItem("active", JSON.stringify(event.target.textContent));
      // console.log(JSON.parse(window.localStorage.getItem("active")));
    },
  },
  computed: {
    songSrc: function () {
      return this.song
        ? `https://music.163.com/song/media/outer/url?id=${this.song.id}.mp3`
        : null;
    },
  },
  watch: {
    currentTime: function () {
      if (this.module !== "单曲") {
        if (this.currentTime && this.duration) {
          if (this.currentTime === this.duration) {
            this.changeSong("next");
          }
        }
      } else {
        if (this.currentTime && this.duration) {
          if (this.currentTime === this.duration) {
            // this.changeSong("next");
            this.$refs.audio.currentTime = 0;
            this.$refs.audio.play();
          }
        }
      }
    },
    song: function (i) {
      this.song
        ? this.axios
            .get("https://apis.netstart.cn/music/lyric", {
              params: {
                id: i.id,
              },
            })
            .then((res) => {
              // this.loading = false;
              this.lyric = res.data.lrc.lyric
                .split("\n")
                .filter((o) => o)
                .map((o) => {
                  var { groups } = o.match(
                    /\[(?<mm>\d{2}):(?<ss>\d{2}\.\d{2,3})\](?<text>.*)/
                  );
                  return {
                    time: groups.mm * 60 + groups.ss * 1,
                    text: groups.text.trim(),
                  };
                });
              // console.log(res.data.lrc);
            })
        : null;
    },
  },
};
</script>

