<template>
  <div class="KoreanSongView">
    <div class="songtitle">
      <div class="titleicon">
        <img src="../assets/titleicon.png" alt="" />
      </div>
      <div
        class="titleinfo"
        @click="
          $emit('update-song-list', chSonglists);
          $emit('showSongList');
        "
      >
        查看全部
      </div>
      <span class="total">{{ kSonglists.length }}</span>
    </div>
    <ul>
      <li
        v-for="(kSonglist, index) in kSonglists"
        :key="kSonglist.id"
        @click="
          $emit('update-song', kSonglist);
          $emit('update-song-list', kSonglists);
        "
      >
        <div class="itemSort">{{ index + 1 }}</div>
        <div class="itmeInfo">
          <div
            class="name"
            :style="{ color: kSonglist.id === songId ? '#00BAFF' : '' }"
          >
            {{ kSonglist?.name }}
          </div>
          <div
            class="arname"
            :style="{ color: kSonglist.id === songId ? '#00BAFF' : '' }"
          >
            <span
              v-for="(artists, index) in kSonglist?.artists"
              :key="artists.id + index"
              >{{ artists.name }}</span
            >
          </div>
        </div>
        <i v-if="kSonglist.id !== songId"></i>
        <i v-else :class="{ playing: playing }" class="active">
          <span></span>
          <span></span>
          <span></span>
          <span></span>
        </i>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    kSonglists: Array,
    songId: [String, Number],
    playing: Boolean,
  },
  data() {
    return {
      // hotSonglists: [],
      // loading: false,
    };
  },
};
</script>

<style lang="less" scoped>
.KoreanSongView {
  padding: 0 20rem;

  .songtitle {
    padding-top: 15rem;
    display: flex;
    align-items: center;
    .titleicon {
      background: rgba(0, 169, 255, 1);
      border-radius: 50%;
      width: 15rem;
      height: 15rem;
      padding: 5rem;
      // display: inline-block;
      box-sizing: content-box;

      display: flex;
      justify-content: center;
      align-items: center;
      img {
        max-width: 22rem;
      }
    }
    .titleinfo {
      margin-left: 10rem;
      font-size: 15.47rem;
    }
    .total {
      margin-left: 10rem;
      padding: 20rem 0 15.82rem;
      font-size: 14.36rem;
      color: #818894;
    }
  }
  ul {
    li {
      display: flex;
      width: 100%;
      justify-content: space-between;
      //   align-content: center;
      height: 73.28rem;
      align-items: center;
      .itemSort {
        font-size: 16.57rem;
        color: #818894;
        width: 2em;
        text-align: center;
        display: flex;
      }
      .itmeInfo {
        width: 80%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        .name {
          font-size: 16.57rem;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
        .arname {
          color: #818894;
          font-size: 13.26rem;
          line-height: 1.5;
          margin-top: 5rem;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          span {
            &::after {
              content: "、";
            }
            &:last-child {
              &::after {
                content: "";
              }
            }
          }
          //   display: flex;
        }
      }
      i {
        background-image: url(https://s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28…=);
        background-size: 166px 97px;
        background-position: -24px 0;
        width: 22rem;
        height: 22rem;
        &.active {
          background-image: none;
          display: flex;
          justify-content: space-around;
          width: 22rem;
          height: 22rem;
          span {
            @keyframes playingrunning {
              from {
                transform: rotateX(15deg);
              }
              to {
                transform: rotateX(60deg);
              }
            }
            width: 11%;
            height: 100%;
            background: #00baff;
            animation: playingrunning 0.8s infinite alternate;
            animation-play-state: paused;
            transform-origin: bottom;
            &:nth-child(1) {
              animation-delay: 0s;
            }
            &:nth-child(2) {
              animation-delay: -0.2s;
            }
            &:nth-child(3) {
              animation-delay: -0.4s;
            }
            &:nth-child(4) {
              animation-delay: -0.6s;
            }
          }
          &.playing {
            span {
              animation-play-state: running;
            }
          }
        }
      }
    }
