<template>
  <div class="hot">
    <div v-show="loading" class="loadingImg">
      <img
        src="data:image/gif;base64,"
        alt=""
      />
    </div>
    <div class="navList">
      <router-link to="/hot"><div class="navItem">华语</div></router-link>
      <router-link to="/hot/en"><div class="navItem">欧美</div></router-link>
      <router-link to="/hot/k"><div class="navItem">韩国</div></router-link>
      <router-link to="/hot/jp"><div class="navItem">日本</div></router-link>
    </div>

    <router-view
      :chSonglists="chSonglists"
      :enSonglists="enSonglists"
      :kSonglists="kSonglists"
      :jpSonglists="jpSonglists"
      :songId="songId"
      :playing="playing"
      @update-song="$emit('update-song', $event)"
      @update-song-list="$emit('update-song-list', $event)"
      @showSongList="$emit('showSongList')"
    ></router-view>
  </div>
</template>

<script>
export default {
  props: {
    songId: [String, Number],
    playing: Boolean,
  },
  data() {
    return {
      chSonglists: [],
      enSonglists: [],
      kSonglists: [],
      jpSonglists: [],
      loading: false,
    };
  },
  created: function () {
    this.loading = true;
    Promise.all([
      this.axios.get("https://apis.netstart.cn/music/top/song?type=7"),
      this.axios.get("https://apis.netstart.cn/music/top/song?type=96"),
      this.axios.get("https://apis.netstart.cn/music/top/song?type=16"),
      this.axios.get("https://apis.netstart.cn/music/top/song?type=8"),
    ]).then(([ch, en, k, jp]) => {
      this.loading = false;
      this.chSonglists = ch.data.data;
      this.enSonglists = en.data.data;
      this.kSonglists = k.data.data;
      this.jpSonglists = jp.data.data;
    });
  },
};
</script>

