<template>
  <li class="home-card">
    <router-link :to="`/cardInfo?id=${card?.id}`">
      <div class="cardImg">
        <img v-if="!card.picUrl" src="../assets/default.png" alt="" class="" />
        <img v-else :src="card ? `${card.picUrl}?imageView=1&type=webp&thumbnail=103x0` : ''" alt="" class="" />
        <span>{{ count }}</span>
      </div>
      <h5>{{ card.name }}</h5>
    </router-link>
  </li>
</template>

<script>
export default {
  props: {
    card: Object,
  },
  computed: {
    count: function () {
      return this.card.playCount > 100000000
        ? `${(this.card.playCount / 100000000).toFixed(1)}亿`
        : this.card.playCount > 10000
        ? `${(this.card.playCount / 10000).toFixed(1)}万`
        : this.card.playCount;
    },
  },
  methods: {
