<template>
  <ul>
    <li
      class="song-item"
      v-for="song in newsongs.slice(0, 6)"
      :key="song.id"
      @click="
        $emit('update-song', song);
        $emit('update-song-list', newsongs);
      "
    >
      <div class="imgBox">
        <img
          v-if="!song.picUrl"
          src="
            ../assets/default.png
          "
          alt=""
        />
        <img
          v-else
          :src="
            song ? `${song.picUrl}?imageView=1&type=webp&thumbnail=103x0` : ''
          "
          alt=""
        />
      </div>
      <div
        class="newsong-title"
        :style="{ color: song.id === songId ? '#00BAFF' : '' }"
      >
        {{ song.name }}
      </div>
      <div
        class="newsong-info"
        :style="{ color: song.id === songId ? '#00BAFF' : '' }"
      >
        {{ song.song.artists[0].name }}
      </div>
      <!-- <div class="newsong-title">{{ song.name }}</div> -->
      <!-- <div class="newsong-info">
        <i class="newsong-sq" v-if="song.song.sqMusic"></i>
        {{ song.song.artists[0].name }} - {{ song.song.album.name }}
      </div> -->
      <!-- <div v-if="song.id !== songId" class="newsong-play"></div>
      <div v-else class="newsong-play active" :class="{ playing: playing }">
        <span></span>
        <span></span>
        <span></span>
        <span></span>
      </div> -->
    </li>
  </ul>
</template>

<script>
export default {
  props: {
    newsongs: Array,

    songId: [Number, String],
    // playing: Boolean,
  },
};
</script>

<style lang="less" scoped>
@keyframes playingrunning {
  from {
    transform: rotateX(15deg);
  }
  to {
    transform: rotateX(60deg);
  }
}
ul {
  // margin-bottom: 40rem;
  display: flex;
  flex-wrap: wrap;
}
li.song-item {
  // padding-left: 10rem;
  // padding: 6rem 10rem;
  margin: 0 auto;
  position: relative;
  .imgBox {
    width: 103px;
    height: 103px;
  }
  img {
    border-radius: 10rem;
  }
  .newsong-title {
    font-size: 12rem;
    width: 103px;
    color: #000;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .newsong-info {
    width: 103px;
    font-size: 12rem;
    color: #818894;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    i.newsong-sq {
      display: inline-block;
      width: 12rem;
      height: 8rem;
      margin-right: 4rem;
      background-image: url("https://s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28c3767992ca4bb6d4887c74880=");
      background-size: 166px 97px;
    }
  }
  .newsong-play {
    background-image: url("https://s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28c3767992ca4bb6d4887c74880=");
    background-position: -24px 0;
    background-size: 166px 97px;
    width: 22rem;
    height: 22rem;
    position: absolute;
    right: 10rem;
    top: 50%;
    transform: translateY(-50%);
    &.active {
      background-image: none;
      display: flex;
      justify-content: space-around;
      span {
        width: 11%;
        height: 100%;
        background: red;
        animation: playingrunning 0.8s infinite alternate;
        animation-play-state: paused;
        transform-origin: bottom;
        &:nth-child(1) {
          animation-delay: 0s;
        }
        &:nth-child(2) {
          animation-delay: -0.2s;
        }
        &:nth-child(3) {
          animation-delay: -0.4s;
        }
        &:nth-child(4) {
          animation-delay: -0.6s;
        }
      }

      &.playing {
        span {
          animation-play-state: running;
        }
      }
    }
  }
}
</style>