<template>
  <div class="playDisc">
    <div class="cdBack" :class="{ play: playing }">
      <div class="ring" :class="{ ringplay: playing }"></div>
      <img
        v-if="song?.al"
        :src="song ? `${song?.al?.picUrl}` : ''"
        alt=""
        :class="{ imgplay: playing }"
      />
      <img
        v-else-if="song?.album"
        :src="song ? `${song?.album?.picUrl}` : ''"
        alt=""
        :class="{ imgplay: playing }"
      />
      <img
        v-else
        :src="song ? `${song.picUrl}` : ''"
        alt=""
        :class="{ imgplay: playing }"
      />
    </div>
    <ul class="s-list" ref="sul">
      <li
        class="s-item"
        v-for="(item, index) in lyric"
        :key="item.text + index"
        ref="sli"
      >
        {{ item.text }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    song: Object,
    playing: Boolean,
    lyric: Array,
    currentTime: Number,
  },
  methods: {
    updateLyric: function () {
      if (this.currentTime !== 0 && this.lyric?.length !== 0) {
        var copy = [...this.lyric].reverse();
        var index =
          copy.findIndex((o) => o.time < this.currentTime) == -1
            ? copy.length - 1
            : copy.findIndex((o) => o.time < this.currentTime);
        var o = {
          index: copy.length - 1 - index,
          text: copy[index]?.text,
        };
        // 所有li的高度放入数组
        var liHeight = [];
        this.$refs.sli.forEach((element, index) => {
          liHeight.push(element.offsetHeight);
          if (index === o.index) {
            element.style.color = "#00baff";
          } else {
            element.style.color = "";
          }
        });
        // liHeight.slice(0, o.index).filter((o) => o != 0).length;
        if (
          // liHeight.slice(0, o.index)
          liHeight.slice(0, o.index).filter((o) => o != 0).length
        ) {
          this.$refs.sul.scrollTop =
            liHeight.slice(0, o.index).reduce((a, b) => {
              return a + b;
            }) - 36.46;
          this.$nextTick(function () {
            this.$refs.sul.scrollTop =
              liHeight.slice(0, o.index).reduce((a, b) => {
                return a + b;
              }) - 36.46;
          });
          // this.$refs.sul.style.marginTop = `translateY(${
          //   o.index * liHeight.slice(0, o.index)
          // }px)`;
          // document.querySelector("ul").style.transform = `translateY(${
          //   o.index * -21 + 150
          // }px)`;
        }
      } else {
        this.$refs.sli.forEach((element, index) => {
          if (index === 0) {
            element.style.color = "#00baff";

            this.$refs.sul.scrollTop = 0;
          } else {
            element.style.color = "";
          }
        });
      }
    },
  },
  watch: {
    currentTime: function () {
      if (this.lyric.length) {
        this.updateLyric();
      }
    },
  },
};
</script>

<style lang="less" scoped>
.playDisc {
  display: flex;
  // flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  // align-items: center;
  width: 100%;
  height: 100%;
  .cdBack {
    width: 223rem;
    height: 223rem;
    background-image: url("../assets/discBackImg1.png");
    background-size: cover;
    background-repeat: no-repeat;
    // font-size: 0;
    position: relative;
    &::after {
      content: "";
      display: block;
      position: absolute;
      right: 0;
      top: 0;
      background-image: url("../assets/discBackImg3.png");
      background-size: cover;
      width: 72.91rem;
      height: 203.11rem;
      transform-origin: 36.455rem 42.704rem;
      transition: all 0.6s ease-in-out;
      transform: rotate(0deg);
    }
    &.play::after {
      transform: rotate(12deg);
    }
    .ring {
      position: absolute;
      left: 14.8%;
      top: 14.8%;
      // transform: translate(-50%, -50%);
      width: 156.89rem;
      height: 156.89rem;
      @keyframes ringRotate {
        0% {
          transform: rotate(-1deg);
        }
        50% {
          transform: rotate(1deg);
        }
        100% {
          transform: rotate(-1deg);
        }
      }
      animation: ringRotate 2s linear infinite;
      animation-play-state: paused;
      &.ringplay {
        animation-play-state: running;
      }
      &::before {
        width: 100%;
        height: 100%;
        display: block;
        content: "";
        position: absolute;
        left: 0;
        top: 2px;
        border-radius: 50%;
        background-image: url("../assets/discBackImg2.png");
        background-size: cover;
      }
    }
    img {
      width: 104.16rem;
      position: absolute;
      left: 59.3712rem;
      top: 61.9752rem;
      // transform: translate(-50%, -50%);
      border-radius: 50%;
      @keyframes imgRotate {
        0% {
          transform: rotate(0deg);
        }
        100% {
          transform: rotate(360deg);
        }
      }
      animation: imgRotate 30s linear infinite;
      animation-play-state: paused;
      &.imgplay {
        animation-play-state: running;
      }
    }
  }
  .s-list {
    height: 108rem;
    width: 100%;
    padding: 0 31.25rem;
    overflow: hidden;
    text-align: center;
    .s-item {
      color: #818894;
      font-size: 14.58rem;
      line-height: 36rem;
      height: 36rem;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
}
</style>