<template>
  <div class="playBar">
    <div class="circleProcess">
      <img
        v-if="song?.al?.picUrl"
        :class="{ playing: playing }"
        :src="
          song ? `${song?.al?.picUrl}?imageView=1&type=webp&thumbnail=60x0` : ''
        "
        alt=""
      />
      <img
        v-else-if="song?.album?.picUrl"
        :class="{ playing: playing }"
        :src="
          song
            ? `${song?.album?.picUrl}?imageView=1&type=webp&thumbnail=60x0`
            : ''
        "
        alt=""
      />
      <img
        v-else-if="song?.picUrl"
        :class="{ playing: playing }"
        :src="song ? `${song.picUrl}?imageView=1&type=webp&thumbnail=60x0` : ''"
        alt=""
      />
      <img src="../assets/default.png" alt="" v-else width="60" />

      <canvas ref="canvas" width="60" height="60"></canvas>
    </div>
    <div class="barMid">
      <h3 class="barSongName" v-if="!song?.name">酷狗音乐</h3>
      <h3 class="barSongName" v-else>{{ song?.name }}</h3>
      <!-- <div class="barsong-info">
        <i class="barsong-sq" v-if="song?.song?.sqMusic"></i>
        {{ song?.song?.artists[0]?.name }} - {{ song?.song?.album?.name }}
      </div> -->
    </div>
    <!-- <span class="barTime">{{ stringcurrentTime }} / {{ stringduration }}</span> -->

    <i
      v-if="!playing"
      @click.stop="$emit('song-play')"
      class="iconfont icon-zanting"
    ></i>
    <i
      v-else
      @click.stop="$emit('song-pause')"
      class="iconfont icon-bofangzhong"
    ></i>
    <i
      class="barList iconfont icon-bofangliebiao"
      @click.stop="$emit('showSongList')"
    ></i>
    <!-- <button
      v-if="!playing"
      @click.stop="$emit('song-play')"
      class="iconfont icon-zanting"
    ></button> -->
    <!-- <button
      v-else
      @click.stop="$emit('song-pause')"
      class="iconfont icon-bofangzhong"
    ></button> -->

    <!-- <button
      class="barList iconfont icon-bofangliebiao"
      @click.stop="$emit('showSongList')"
    ></button> -->
  </div>
</template>

<script>
export default {
  props: {
    song: Object,
    duration: Number,
    currentTime: Number,
    playing: Boolean,
  },
  computed: {
    stringcurrentTime: function () {
      return (
        String(Math.floor(this.currentTime / 60)).padStart(2, "0") +
        ":" +
        String(Math.floor(this.currentTime % 60)).padStart(2, "0")
      );
    },
    stringduration: function () {
      return (
        String(Math.floor(this.duration / 60)).padStart(2, "0") +
        ":" +
        String(Math.floor(this.duration % 60)).padStart(2, "0")
      );
    },
    percentage: function () {
      return this.currentTime / this.duration;
    },
  },
  watch: {
    percentage: function (n) {
      // console.log(n);

      var canvas = this.$refs.canvas;
      var context = canvas.getContext("2d");

      context.clearRect(0, 0, 60, 60);

      context.beginPath();
      context.arc(30, 30, 28, -0.5 * Math.PI, Math.PI * (2 * n - 0.5));
      // context.closePath(); 
      context.strokeStyle = "#00baff";
      context.stroke();
      context.lineWidth = 4;
    },
  },
};
</script>

<style lang="less" scoped>
@keyframes rotate {
  form {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.playBar {
  // width: calc(100% - 30rem);
  width: 92%;
  display: flex;
  // padding: 10rem;
  justify-content: space-between;
  align-items: center;
  background-color: #f1f3f4;
  height: 60rem;
  position: fixed;
  bottom: -1px;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 30rem;

  padding-right: 10rem;
  z-index: 999;
  .circleProcess {
    position: relative;
    width: 60rem;
    height: 60rem;
    display: flex;
    img {
      margin: auto;
      border-radius: 50%;
      animation: rotate 8s linear infinite;
      animation-play-state: paused;
      &.playing {
        animation-play-state: running;
      }
    }
    canvas {
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
      position: absolute;
      // box-shadow: 0 0 3px 0 red;
    }
  }
  .barMid {
    display: flex;
    flex-direction: column;
    width: 50%;
    h3.barSongName {
      font-size: 17rem;
      color: #333333;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      width: 100%;
    }
    .barsong-info {
      font-size: 12rem;
      color: #888888;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      width: 100%;
      i.barsong-sq {
        display: inline-block;
        width: 12rem;
        height: 8rem;
        margin-right: 4rem;
        background-image: url("https://s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28c3767992ca4bb6d4887c74880=");
        background-size: 166px 97px;
      }
    }
  }

  i {
    width: 30rem;
    font-size: 30rem;
  }
  // button {
  //   background: transparent;
  //   border: none;
  //   outline: none;
  // }

  // .barList {
  // }
}
</style>